@font-face {
    font-family: 'tga';
    src: url('../fonts/tga_bold.otf') format('woff2');
}

.section {
    width: 100vw;
    height: fit-content;
    /* background-image: url("../svgs/Cloudy.svg"); */
}

.container {
    display: flex;
    flex-direction: column;
    width: 100vw;
}

.title {
    font-family: 'tga';
    font-size: 5vw;
    color: #F2E7D5;
    padding-bottom: 2vw;
    text-align: center;
    text-shadow:
        -2px -2px 0 #393E46,
        0   -2px 0 #393E46,
        2px -2px 0 #393E46,
        2px  0   0 #393E46,
        2px  2px 0 #393E46,
        0    2px 0 #393E46,
        -2px  2px 0 #393E46,
        -2px  0   0 #393E46;
}

.portfolio_grid {
    display: grid;
    grid-template-columns: repeat(4, 5fr);
    row-gap: 2rem;
    width: 100%;
}

.box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.screenshot {
    background-color: #6D9886;
    background-image: url("../svgs/Cloudy.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 19rem 11rem;
    border-radius: 20px;
    width: 20rem;
    height: 12rem;
    margin:0;
    box-shadow: 4px 4px 2px #393E46;
}

.caption {
    font-family: 'tga';
    font-size: 1.35vw;
    text-align: center;
    color: #F2E7D5;
    width: 20rem; 
    margin: 0;
    text-shadow:
        -2px -2px 0 #393E46,
        0   -2px 0 #393E46,
        2px -2px 0 #393E46,
        2px  0   0 #393E46,
        2px  2px 0 #393E46,
        0    2px 0 #393E46,
        -2px  2px 0 #393E46,
        -2px  0   0 #393E46;
}

@media only screen and (max-width: 1300px) {
    .portfolio_grid {
        grid-template-columns: repeat(3, 1fr);
    }
}


@media only screen and (max-width: 1000px) {
    .title {
        padding: 0;
        padding-bottom: 10vw;
        margin: auto;
    }

    .portfolio_grid {
        grid-template-columns: repeat(2, 1fr);
    }

    .caption {
        font-size: 4vw;
    }

    .title {
        font-size: 10vw;
    }
}

@media only screen and (max-width: 800px) {
    .portfolio_grid {
        grid-template-columns: none;
    }
}