@font-face {
    font-family: 'tga';
    src: url('../fonts/tga_bold.otf') format('woff2');
}

.section {
    width: 100vw;
    padding-bottom: 10rem;
}

.container {
    display: flex;
    flex-direction: column;
    width: 100%;
    color: #F2E7D5;
    margin: auto;
}

.job_container {
    display: flex;
    flex-direction: column;
    width: 100vw;
}

.bullets_container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.title_container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    padding-left: 8vw;
    margin: auto;
}

.header {
    font-family: 'tga';
    margin: auto;
    font-size: 5vw;
    text-shadow:
        -3px -3px 0 #393E46,
        0   -3px 0 #393E46,
        3px -3px 0 #393E46,
        3px  0   0 #393E46,
        3px  3px 0 #393E46,
        0    3px 0 #393E46,
        -3px  3px 0 #393E46,
        -3px  0   0 #393E46;
}

.p {
    margin: 0;
    padding: 0;
    text-shadow:
        -1.5px -1.5px 0 #393E46,
        0   -1.5px 0 #393E46,
        1.5px -1.5px 0 #393E46,
        1.5px  0   0 #393E46,
        1.5px  1.5px 0 #393E46,
        0    1.5px 0 #393E46,
        -1.5px  1.5px 0 #393E46,
        -1.5px  0   0 #393E46;
}

.job_title {
    font-family: 'tga';
    font-size: 1.5vw;
    color: #6D9886;
    text-shadow:
        -1.5px -1.5px 0 #393E46,
        0   -1.5px 0 #393E46,
        1.5px -1.5px 0 #393E46,
        1.5px  0   0 #393E46,
        1.5px  1.5px 0 #393E46,
        0    1.5px 0 #393E46,
        -1.5px  1.5px 0 #393E46,
        -1.5px  0   0 #393E46;
}

.date {
    font-family: 'tga';
    font-size: 1.25vw;
    color: #4a6a71;
    text-shadow:
        -1.5px -1.5px 0 #393E46,
        0   -1.5px 0 #393E46,
        1.5px -1.5px 0 #393E46,
        1.5px  0   0 #393E46,
        1.5px  1.5px 0 #393E46,
        0    1.5px 0 #393E46,
        -1.5px  1.5px 0 #393E46,
        -1.5px  0   0 #393E46;
}

.brand {
    font-family: 'tga';
    font-size: 1.25vw;
    color: #5d8982;
    text-shadow:
        -1.5px -1.5px 0 #393E46,
        0   -1.5px 0 #393E46,
        1.5px -1.5px 0 #393E46,
        1.5px  0   0 #393E46,
        1.5px  1.5px 0 #393E46,
        0    1.5px 0 #393E46,
        -1.5px  1.5px 0 #393E46,
        -1.5px  0   0 #393E46;
}

.bullet {
    font-family: 'tga';
    font-size: 1.35vw;
    color: #F2E7D5;
    text-shadow:
        -1.5px -1.5px 0 #393E46,
        0   -1.5px 0 #393E46,
        1.5px -1.5px 0 #393E46,
        1.5px  0   0 #393E46,
        1.5px  1.5px 0 #393E46,
        0    1.5px 0 #393E46,
        -1.5px  1.5px 0 #393E46,
        -1.5px  0   0 #393E46;
}

.subtitle {
    display: flex;
    justify-content: center;
    font-family: 'tga';
    font-size: 2vw;
    margin: auto;
    color: #5d8982;
    text-shadow:
        -1.5px -1.5px 0 #393E46,
        0   -1.5px 0 #393E46,
        1.5px -1.5px 0 #393E46,
        1.5px  0   0 #393E46,
        1.5px  1.5px 0 #393E46,
        0    1.5px 0 #393E46,
        -1.5px  1.5px 0 #393E46,
        -1.5px  0   0 #393E46;
}


@media only screen and (max-width: 1000px) {
    .container {
        padding-top: 12vw;
    }

    .header {
        padding: 0;
        text-align: center;
        font-size: 10vw;
    }

    .container {
        margin: 0;
        width: 100vw;
    }

    .job_container {
        width: 100vw;
    }

    .subtitle {
        justify-content: center;
        width: 100vw;
    }

    .title_container {
        padding-top: 10vw;
        padding-left: 5vw;
    }

    .bullets_container {
        width: 80vw;
        padding-left: 10vw;
    }
    .bullet {
        font-size: 4vw;
    }

    .subtitle {
        font-size: 5vw;
    }

    .job_title, .brand, .date {
        font-size: 4.5vw;
    }
}