@font-face {
    font-family: 'tga';
    src: url('../fonts/tga_bold.otf') format('woff2');
}

.section {
    width: 100%;
    background-image: url("../svgs/Cloudy.svg");
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: cover;
}

.container {
    display: flex;
    justify-content: space-around;
    transform: translateY(-4rem);
}

.header_container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 35vw;
    justify-content: flex-end;
}

.discord_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.email_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.header {
    text-align: center;
    font-family: 'tga';
    font-size: 5vw;
    color: #393E46;
    height: 6vw;
}

.subtitle {
    text-align: center;
    font-family: 'tga';
    font-size: 1.25vw;
    color: #393E46;
}


.discord_icon {
    background-image: url("../svgs/discord-icon.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 5vw;
    width: 5vw;
    height: 5vw;
}

.email_icon {
    background-image: url("../svgs/email-icon.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: 4.5vw;
    width: 5vw;
    height: 5vw;
}

.text {
    font-family: 'tga';
    text-align: center;
    color: #393E46;
}

@media only screen and (max-width: 1300px) {
    .header_container {
        transform: translateY(3vw);
    }

    .container {
        transform: translateY(-4vw);
    }
}
