@font-face {
    font-family: 'tga';
    src: url('../fonts/tga_bold.otf') format('woff2');
}

.wrapper {
    position: fixed;
    padding: .4rem;
    height: 2rem;
    width: 100vw;
    box-shadow: 1px -3px 10px 2px black;
    background-color: #6D9886;
    z-index: 100;
}

.container {
    display: flex;
    width: auto;
    height: 2rem;
    margin: 0 2rem;
    justify-content: center;
    align-items: center;
}

.element {
    display: flex;
    font-family: 'tga';
    width: fit-content;
    height: 2rem;
    margin: 0 .3rem;
    padding: 0 1rem;
    align-items: center;
    justify-content: center;
    background-color: #F2E7D5;
    color: #6D9886;
    border-radius: .3rem;
    -moz-user-select: -moz-none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    user-select: none;
    box-shadow: 2px 3px 0px #393E46;
}

.element:hover {
    cursor: pointer;
}

