@font-face {
    font-family: 'tga';
    src: url('../fonts/tga_bold.otf') format('woff2');
}

.section {
    display: flex;
    align-items: center;
    width: 100vw;
    height: 720px;
    padding-bottom: 2rem;
    /* background-image: url("../svgs/blob_scene_1.svg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    overflow: visible; */
}

.container {
    display: flex;
    flex-direction: column;
    text-align: center;
    /* background-color: rgba(242, 231, 213, .5);
    border-radius: 50px; */
}

.header {
    font-family: 'tga';
    font-size: 5vw;
    margin: 0;
    /* color: #393E46; */
    color: #F2E7D5;
    text-shadow:
        -3px -3px 0 #393E46,
        0   -3px 0 #393E46,
        3px -3px 0 #393E46,
        3px  0   0 #393E46,
        3px  3px 0 #393E46,
        0    3px 0 #393E46,
        -3px  3px 0 #393E46,
        -3px  0   0 #393E46;
}

.p {
    padding-top: 2rem;
    font-family: 'tga';
    font-size: 2vw;
    margin: auto;
    width: 80%;
    color: #F2E7D5;
    text-shadow:
        -3px -3px 0 #393E46,
        0   -3px 0 #393E46,
        3px -3px 0 #393E46,
        3px  0   0 #393E46,
        3px  3px 0 #393E46,
        0    3px 0 #393E46,
        -3px  3px 0 #393E46,
        -3px  0   0 #393E46;
}


@media only screen and (max-width: 1000px) {
    .header {
        padding-top: 20vw;
        font-size: 8vw;
    }

    .p {
        font-size: 4vw;
    }
}

@media only screen and (max-width: 600px) {
    .header {
        padding-top: 25w;
        font-size: 10vw;
    }

    .p {
        font-size: 4vw;
    }
}

